






import Swal from 'sweetalert2';
import { Component, Vue } from 'vue-property-decorator';
import { Action, State } from 'vuex-class';
import { IInputInfo, ILoginPayload } from '@/types/interface';

@Component
export default class AssociationTemplate extends Vue {
  @State('userId') stateUserId!: string;
  @State('source') stateSource!: string;
  @State('approveKey') stateApproveKey!: string;
  @State('phone') statePhone!: string;
  @State('email') stateEmail!: string;
  @State('name') stateName!: string;
  @Action('addMember') addMember!: (params: IInputInfo) => Promise<ILoginPayload>;

  isAssociated = false;

  get isMemberProcess() {
    return this.$route.query.process === 'member';
  }

  created() {
    if (this.isMemberProcess) {
      this.isAssociated = true;
      window.addEventListener('message', this.receiveMessage, false);
    } else {
      Swal.fire({
        width: 300,
        showCloseButton: false,
        showCancelButton: true,
        background: '#FFFFFF',
        title: '是否關聯公職王帳號',
        text: '',
        customClass: {
          title: 'modal-title',
          content: 'modal-content',
          confirmButton: 'modal-confirm-button-half',
          cancelButton: 'modal-cancel-button-half',
        },
        confirmButtonText: '關聯',
        cancelButtonText: '略過',
        reverseButtons: true,
      }).then((res) => {
        if (res.isConfirmed) {
          this.isAssociated = true;
          window.addEventListener('message', this.receiveMessage, false);
        } else this.$router.push('/login/set_member_profile');
      });
    }
  }

  receiveMessage(e: any) {
    if (e.origin === 'https://www.public.com.tw') {
      const params = {
        Status: '1',
        UserGuid: this.stateUserId,
        PID: e.data.Data.id,
        Source: this.stateSource,
        // eslint-disable-next-line
        Approve_Key: this.stateSource === 'CF01' || this.stateSource === 'CF02' ? this.stateApproveKey : undefined,
        Email: this.stateSource === 'CF01' || this.stateSource === 'CF02' ? this.stateEmail : undefined,
        Name: this.stateSource === 'CF01' || this.stateSource === 'CF02' ? this.stateName : undefined,
        Mobile: this.stateSource === 'CF05' ? this.statePhone : undefined,
      };
      this.addMember(params)
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            if (this.isMemberProcess) this.$router.push('/member/edit_profile');
            else this.$router.push('/login/set_member_profile');
          }
        });
    }
  }
}
